import axios from "axios";

export class UserAPI {

    constructor() {
        this.url = "https://jsonplaceholder.typicode.com/users";
    }

    getUsers = async () => {
        try {
            const response = await axios.get(this.url);
            return response.data;
        } 
        catch (error) {
            console.log(error);
        }
    }
}