import React from "react";

export default function UserDetails(props) {
  return (
    <div>
      <h1>COMP3123 Assignment 2</h1>
      <h2>ReactJS</h2>
      <h3>User Details</h3>
      <p>
        <b>User Name : </b> {props.name}
        <b>User Email : </b> {props.email}
        <b>User Password</b> {props.password}
      </p>
    </div>
  );
}