import UserDetails from "./UserDetails";
import { UserAPI } from "./network/UserAPI";

export default class UserList extends Component {

    constructor(props) {

        super(props);

        this.UserAPI = new UserAPI();

        this.state = {
            users: [],
            userid: 0
        };
    }

    componentDidMount() {
        this.getUserData();
    }

    // Get user data from API and set state

    getUserData = async () => {

        try {
            const response = await axios.get("https://jsonplaceholder.typicode.com/users");
            this.setState({ users: response.data });
        } catch (error) {
            console.log(error);
        }
    };


    // Create Nwe User

    createNewUser = async () => {
        try {
            const response = await axios.post("https://jsonplaceholder.typicode.com/users", {
                first_name: "New User",
                last_name: "New User",
                email: "new.user@gmail.com"
            });
            this.getUserData(response.data);
        } catch (error) {
            console.log(error);
        }
    };

    // Get User data by ID

    getUserDataById = async (id) => {

        try {
            const res = await axios.get(`https://jsonplaceholder.typicode.com/users/${id}`)
            .then(res => {
                console.log(res.data)
            })
        } catch (error) {
            console.log(error);
        }
    };

    // Read User by ID

    readUserById = event => {
        this.state({...this.state, userid: event.target.value});
    };

    // Get User by New ID

    getUserById = () => {
        this.getUserDataById(this.state.userid);
    };

    // Update User by ID

    updateUserById = async (id) => {
        try {
            const response = await axios.put(`https://jsonplaceholder.typicode.com/users/${id}`, {
                first_name: "Updated User",
                last_name: "Updated User",
                email: "updated@email.com"
            });
            this.getUserData(response.data);
        } catch (error) {
            console.log(error);
        }
    };

    // Delete User by ID

    deleteUserById = async (id) => {
        try {
            const res = await axios.delete(`https://jsonplaceholder.typicode.com/users/${id}`)
            .then(response => {
                console.log(response.data);
                let users = this.state.users.filter(user => user.id !== id);
                this.setState({ users })
                this.getUserData(res.data);
            })

        } catch (error) {
            console.log(error);
        }
    };

    render() {

       return (
            <div>
                
                <button onClick={this.createNewUser}>Create New User</button>
                <button onClick={this.getUserData}>Get Users</button>
                {
                    this.state.users.map(u => (
                        <div key={u.id}>
                            <UserDetails  user = {u}/>
                            <button onClick={ (e) => this.getUserDataByID(u.id)}>Get Users By ID</button>
                            <button onClick={ (e) => this.deleteUserDataByID(u.id)}>Delete</button>
                        </div>
                    ))
                }
                <input onChange={this.readUserID} type="text" placeholder="Enter User ID"/>
                <button onClick={ this.getUserByID }>Get By ID</button>
            </div>
        ) 
    }
}
